@import url('https://fonts.googleapis.com/css2?family=Quicksand&display=swap');

/* == base styles == */

*{
  font-family: 'Quicksand', sans-serif;
  margin: 0;
  color: #333;
}

.navbar{
  padding:20px;
  display:flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}

.navbar h1{
  color: tomato;
}

.navbar .navlinks {
  margin-left: auto;
}

.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding:6px;
}

.navbar a:hover {
  color: tomato;
}

.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

/* == blog previews == */
.blog_preview{
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}

.blog_preview:hover{
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}

.blog_preview h2{
  font-size: 20px;
  color: tomato;
  margin-bottom: 8px;
}

.blog_preview a {
  text-decoration: none ;
}

/* Blog details page */

.blog-details h2 {
  font-size: 20px;
  color: tomato;
  margin-bottom: 10px;
}

.blog-details div {
  margin: 20px 0;
}

.blog-details button {
  background: tomato;
  color:ghostwhite;
  border:0;
  padding:8px;
  border-radius:8px;
  cursor: pointer;
}

/* Create new blog form */

.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}

.create label {
  text-align: left;
  display: block;
}

.create h2 {
  font-size: 20px;
  color: tomato;
  margin-bottom: 30px;
}

.create input,
.create textarea,
.create select {
  width:100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}

.create button {
  background: tomato;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}